@font-face {
    font-family: 'Nunito Sans';
    src:url('../fonts/NunitoSans-Bold.woff2') format('woff2'),
       url('../fonts/NunitoSans-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

